html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Inria Serif, serif;
}

.header {
  clip-path: polygon(0 0, 30vw 0, 0 30vw);
  width: 100vw;
  height: 100vw;
  color: #fff;
  mix-blend-mode: multiply;
  background-color: #75135d;
  font-size: 5vw;
  position: fixed;
}

.header__logo {
  width: 10vw;
  height: 10vw;
  border: 2px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 3vw;
  display: flex;
}

.parallax {
  height: 100vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  justify-content: center;
  align-items: center;
  display: flex;
}

.parallax--1 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("El_Condoriri_trek.21908014.jpeg");
}

.parallax--2 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("Illiniza_Norte.c9946892.jpeg");
}

.parallax--3 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("El_Chachani_summit.112ae70b.jpeg");
}

.parallax__text {
  color: #ffffffb3;
  font-size: 6.4rem;
}

.banner {
  height: 100%;
  background-color: #f5f5f5;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.banner__content {
  color: #a2a2a2;
  width: 16%;
  text-align: left;
  margin-right: 6rem;
  padding: 2rem;
  font-size: 2.4rem;
}

.banner__personal-details {
  color: #75135db3;
  transform-origin: 0 0;
  align-self: flex-end;
  margin-left: 1rem;
  font-family: Muli, sans-serif;
  font-size: 1.7rem;
  transform: rotate(-90deg);
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 800px) {
  .parallax {
    height: 80vh;
    background-attachment: scroll;
    align-items: flex-start;
  }

  .parallax--2 {
    background-image: url("Illiniza_Norte.c9946892.jpeg");
  }

  .parallax--3 {
    background-image: url("El_Chachani_summit.112ae70b.jpeg");
  }

  .parallax__text {
    max-width: 90%;
  }

  .parallax__text--1 {
    margin-top: 20vh;
  }

  .parallax__text--2 {
    margin-top: 2vh;
  }

  .parallax__text--3 {
    margin-top: 4vh;
  }

  .banner {
    min-height: 30vw;
    height: 25vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    display: flex;
  }

  .banner__content {
    max-width: 500px;
    width: 90%;
    text-align: center;
    margin-right: 0;
    padding: 2rem;
  }

  .banner__personal-details {
    transform-origin: 0 0;
    align-self: center;
    margin-top: 2rem;
    margin-left: 0;
    font-size: 1.7rem;
    transform: rotate(0deg);
  }
}

@media only screen and (max-width: 700px) {
  .parallax__text {
    font-size: 4.4rem;
  }
}

